<script>
import { getyzm, login, register } from "../api/html.js";
export default {
  data() {
    return {
      showlogin: true,
      // isButtonDisabled: false,
      codes: '',
      // countdown: 60,
      zhuce: "",
      login: "",
      // 验证信息
      ruleForm: {
        name: '',
        region: '',
        desc: '',
      },
    };
  },
  mounted() {
    this.generateVerifyCode();
  },
  methods: {
    submitForm() {
      login({
        name: this.ruleForm.name,
        pwd: this.ruleForm.region,
        vercode: this.codes,
      })
        .then(res => {
          console.log(res);
          if (res.code === 0) {
            // 跳转页面
            this.$router.push('/shouye')
            // 清空数据
            this.ruleForm.name = "";
            this.ruleForm.region = "";
            this.ruleForm.desc = "";
          }
        })

    },
    // 注册
    yesform() {
      const formData = {
        name: this.ruleForm.names,
        pwd: this.ruleForm.regions,
        emails: this.ruleForm.email
      };
      register(formData)
        .then(res => {
          console.log(res);
          if (res.code == 0) {
            alert('注册成功！');
            this.showlogin = true;
            // 清空数据
            this.ruleForm.names = "";
            this.ruleForm.regions = "";
            this.ruleForm.email = "";
          } else {
            alert('注册数据不能为空,请重试！');
          }
        })
        .catch(err => {
          console.error('注册失败:', err);
          alert('注册失败，请重试！');
        });

    },
    // 注册 将盒子显示或隐藏
    resetForm() {
      if (this.showlogin == true) {
        this.showlogin = false;
      } else if (this.showlogin == false) {
        this.showlogin = true;
      }
    },
    // 验证码
    generateVerifyCode() {
      console.log(111);
      getyzm()
        .then(res => {
          console.log(res.msg);
          this.codes = res.msg;
        })
        .catch(error => {
          console.error('Error fetching code:', error);
        });
      // var times = 60
      // this.asd = setInterval(() => {
      //   times--
      //   document.getElementById("a").innerHTML = times + '秒后重新获取'
      //   // 禁止
      //   document.getElementById("a").setAttribute('disabled', true)
      //   // 清除定时器
      //   if (times == 0) {
      //     document.getElementById("a").innerHTML = '生成验证码'
      //     clearInterval(this.asd)
      //     document.getElementById("a").removeAttribute('disabled', true)
      //     times = 5
      //   }
      // }, 1000);
    },
  },
}

</script>
